// config.js - Configuration for Cloud Foundry deployment

const getApiBaseUrl = () => {
  // For Cloud Foundry production
  if (window.location.hostname.includes('cfapps.us10.hana.ondemand.com')) {
    // Get the backend URL from environment or hardcode it
    // You'll need to replace this with your actual backend URL
    return 'https://dpfna-sbx-sbx-vector-backend.cfapps.us10-001.hana.ondemand.com';
  }
  
  // For local development
  if (window.location.hostname === 'localhost') {
    return 'http://localhost:5003/api';
  }
  
  // Default - use relative path (if same domain)
  return '/api';
};

export const API_BASE_URL = getApiBaseUrl();